.heading {
  font: Inter;
  font-weight: 500;
  size: 18px;
  line-height: 25.2px;
  color: #1c4980;
}
.main {
  border-radius: 12px;
  border: 1px solid #dadce0;
  display: flex;
  display: grid;
  grid-template-columns: 15% 30% 40% 15%;
}

.firstBox {
  border-right: 1px solid #dadce0;
  padding-top: 16px;
  padding-right: 20px;
  padding-bottom: 16px;
  padding-left: 20px;
}
@media (max-width: 500px) {
  .firstBox {
    border-right: none;
  }
}

h2 {
  font-family: Inter;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0px;
  text-align: left;
  color: #1c4980;
}
.flex {
  display: flex;
  height: 40px;
  width: 77px;
}
.firstBoxIcon {
  height: 40px;
  width: 40px;
  border-radius: 8px;
  background-color: #ebe8fd;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 500px) {
  .firstBoxIcon {
    height: 30px;
    width: 30px;
  }
}
.icon {
  width: 20px;
  height: 20px;
  color: #6548ee;
}
@media (max-width: 390px) {
  .icon {
    height: 15px;
    width: 15px;
  }
}
.spanHead {
  font-family: Inter;
  font-size: 20px;
  font-weight: 700;
  align-items: center;
  display: flex;
  color: #1c4980;
  padding-left: 10px;
}
@media (max-width: 500px) {
  .spanHead {
    font-size: 15px;
    font-weight: 700;
  }
}
@media (max-width: 360px) {
  .spanHead {
    font-size: 12px;
    font-weight: 700;
  }
}
.secondBox {
  /* width: 369px; */
  border-right: 1px solid #dadce0;
  padding-top: 16px;
  padding-right: 20px;
  padding-bottom: 16px;
  padding-left: 20px;
}

.spanHead1 {
  font-family: Inter;
  font-size: 20px;
  font-weight: 700;
  color: #1c4980;
  padding-left: 10px;
}
@media (max-width: 500px) {
  .spanHead1 {
    font-size: 15px;
    font-weight: 700;
  }
}
.spanHead1 span {
  font-family: Inter;
  font-size: 12px;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: 0px;
  text-align: left;
  color: #05c165;
}

.candidteflex {
  display: flex;
}
.totalCan {
  font-family: Inter;
  font-size: 12px;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: 0px;
  text-align: left;
  color: #1c4980;
}
.line {
  margin-left: 1rem;
  margin-right: 1rem;
  height: 40px;
  border-left: 1px solid #dadce0;
}
@media (max-width: 500px) {
  .line {
    margin-left: 10px;
    margin-right: 10px;
  }
}

.thirdBox {
  /* width: 499px; */
  border-right: 1px solid #dadce0;
  padding-top: 16px;
  padding-right: 20px;
  padding-bottom: 16px;
  padding-left: 20px;
}

.fourthBox {
  /* width: 188px; */
  border-right: 1px solid #dadce0;
  padding-top: 16px;
  padding-right: 20px;
  padding-bottom: 16px;
  padding-left: 20px;
}

@media (max-width: 925px) {
  .main {
    grid-template-columns: 30% 70%;
  }
  .firstBox:nth-child(3) {
    order: 4; /* Change position of 3rd firstBox to 4th position */
  }

  .firstBox:nth-child(4) {
    order: 3; /* Change position of 4th firstBox to 3rd position */
  }
}
@media (max-width: 500px) {
  .main {
    grid-template-columns: 50% 50%;
  }
  .firstBox:nth-child(2) {
    order: 4; /* Change position of 3rd firstBox to 4th position */
  }

  .firstBox:nth-child(4) {
    order: 2; /* Change position of 4th firstBox to 3rd position */
  }

  .firstBox:nth-child(3) {
    border-top: 1px solid #dadce0;
    grid-column: 1 / span 3; /* Span the grid item across 3 columns */
  }
  .firstBox:nth-child(2) {
    border-top: 1px solid #dadce0;
    grid-column: 1 / span 3; /* Span the grid item across 3 columns */
  }
}
