:root {
  --navBg: #f2f8fe;
}

.navbar {
  background-color: var(--navBg);
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.navbarHead {
  padding-left: 30px;
}
.menu-bars {
  padding-right: 22px;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: var(--navBg);
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0 8px 28px;
  list-style: none;
  height: 60px;
  font-size: 15px;
  font-weight: 500;
  line-height: 16.8px;
  color: #1c4980;
}
.nav-text:hover {
  background-color: #e5f1fc;
  border: 1px solid #0073e6;
  margin: 3rem;
  border-radius: 8px;
}
.btn {
  width: Hug (47px);
  height: Fixed (18px);
  padding: 6px 8px 6px 8px;
  border-radius: 22px;
  border: 1px;
  gap: 6px;
  color: #d63500;
  background-color: #fbebea;
  border: 1px solid #d63500;
}

.nav-menu-items {
  width: 100%;
  padding: 0;
}

.navbar-toggle {
  background-color: var(--navBg);
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 1rem;
  /* padding: 2rem; */
}

span {
  margin-left: 16px;
}

