@media (width>500px) {
  .hiddenComponent {
    display: none;
  }
}

@media (max-width: 500px) {
  .assessemntOverview-hide-show {
    display: none;
  }
}
.myassessment {
  display: grid;
  grid-template-columns: 50% 50%;
}
.iconsFlex {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  float: right;
  align-items: center;
}
.iconFlex-icon {
  display: flex;
  justify-content: flex-end;
}
.hide-show {
  display: none;
}

@media (max-width: 500px) {
  .hide-show {
    display: block;
  }
}

.cardsMainBox {
  display: grid;
  /* text-align: center; */
  grid-template-columns: 33% 33% 33%;
  grid-gap: 10px;
}
@media (max-width: 768px) {
  .cardsMainBox {
    grid-template-columns: 50% 50%;
  }
}
@media (max-width: 600px) {
  .cardsMainBox {
    grid-template-columns: 100%;
  }
}

.newAssissmentBox {
  border: 1px dashed #dadce0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f6f8fa;
  padding: 30px;
  border-radius: 12px;
}
.newAssissmentBox span {
  color: #1c4980;
  font-family: Inter;
  font-size: 18px;
  font-weight: 500;

  text-align: center;
}
.newAssissmentBox p {
  color: #1c4980;
  font-family: Inter;
  font-size: 12px;
  font-weight: 500;

  text-align: center;
}
.Plusicon {
  width: Fixed (70px);
  height: Fixed (70px);
  padding: 10px;
  border-radius: 72px;

  background-color: white;
}
.cards2Box {
  /* width:388px; */
  height: Hug (209px);
  padding: 16px;
  border-radius: 12px;
  border: 1px solid #dadce0;
}
.secondCard {
  display: flex;
  justify-content: space-between;
}
.secondBoxIcon {
  height: 40px;
  width: 40px;
  border-radius: 8px;
  background-color: #ebe8fd;
  display: flex;
  justify-content: center;
  align-items: center;
}

.secondicon {
  width: 20px;
  height: 20px;
  color: #6548ee;
}

.math {
  font-family: Inter;
  font-size: 18px;
  font-weight: 500;
  line-height: 25px;
  letter-spacing: 0px;
  text-align: left;
  color: #1c4980;
}
.calicon {
  color: #1c4980;
  width: 11.6px;
  height: 12.83px;
  top: 0.58px;
  left: 1.16px;
}
.duration {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px dotted #dadce0;
  display: flex;
  justify-content: space-between;
}
.shareBtn {
  width: 77px;
  height: 30px;
  padding-top: 2px;
  padding-right: 8px;
  padding-bottom: 2px;
  padding-left: 6px;
  border-radius: 50px;
  border: 1px solid #1c4980;
  gap: 4px;
  color: #1c4980;
}
.lp {
  position: relative;
  height: 24px;
  padding-top: 2px;
  padding-right: 8px;
  padding-bottom: 2px;
  padding-left: 6px;
  border-radius: 50px;
  border: 1px;
  background-color: #6548ee;
  color: white;
  margin-left: 10px;
  margin-right: 20px;
}
.lp1 {
  position: absolute;
  height: 24px;
  padding-top: 2px;
  padding-right: 8px;
  padding-bottom: 2px;
  padding-left: 6px;
  border-radius: 50px;
  border: 1px;
  background-color: #3079e1;
  color: white;
  margin-left: 113px;
}
.lp2 {
  position: absolute;
  height: 24px;
  padding-top: 2px;
  padding-right: 8px;
  padding-bottom: 2px;
  padding-left: 6px;
  border-radius: 50px;
  border: 1px;
  background-color: #e9407a;
  color: white;
  margin-left: 123px;
}

.question {
  color: #1c4980;
  font-size: 12px;
  font-weight: 500;
}

.createHeading {
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
  color: #1c4980;
  margin-left: 30px;
}
form {
  max-width: 300px;
  margin: 10px auto;
  padding: 10px 20px;
  background: white;
  border-top: 1px solid #dadce0;
}

h1 {
  margin: 0 0 30px 0;
  text-align: center;
}

input[type="text"],
input[type="password"],
input[type="date"],
input[type="datetime"],
input[type="email"],
input[type="number"],
input[type="search"],
input[type="tel"],
input[type="time"],
input[type="url"],
textarea,
select {
  border: 1px solid #dadce0;
  border-radius: 8px;
  font-size: 16px;
  height: auto;
  margin: 0;
  outline: 0;
  width: 100%;
  height: 40px;
  color: #dadce0;
  margin-bottom: 25px;
  margin-top: 5px;
}

input[type="radio"],
input[type="checkbox"] {
  margin: 0 4px 8px 0;
}

select {
  padding: 6px;
  height: 32px;
  border-radius: 2px;
  height: 45px;
  border: 1px solid #dadce0;
  border-radius: 8px;
}

.button {
  color: #fff;
  background-color: #0073e6;
  font-size: 18px;
  text-align: center;
  font-style: normal;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 10px;
  height: 40px;
}

fieldset {
  border: none;
}

legend {
  font-size: 1.4em;
  margin-bottom: 10px;
}

label {
  display: block;
  margin-bottom: 8px;
}

label.light {
  font-weight: 300;
  display: inline;
}

.number {
  background-color: #5fcf80;
  color: #fff;
  height: 30px;
  width: 30px;
  display: inline-block;
  font-size: 0.8em;
  margin-right: 4px;
  line-height: 30px;
  text-align: center;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
  border-radius: 100%;
}

@media screen and (min-width: 480px) {
  form {
    max-width: 580px;
  }
}
.label {
  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0em;
  text-align: center;
  color: #1c4980;
}
